.menu--nav-worker {
  display: flex;
  align-items: center;
  color: white;
  width: 100%;
}

.menu--nav-worker img {
  width: 60px; /* Ajusta el tamao de la imagen segn sea necesario */
  margin-left: 20px; /* Aade un margen derecho para separar la imagen del ttulo */
}

.notify-worker{
  position: relative;
  background: #c62426;
  border-radius: 10px;
  color: #fff;
  z-index: 1;
  top: 0;
  margin-left: auto;
  margin-right: 40px;
}

.notify-worker .icon {
  font-size: 30px;
  transition: 0.2s ease-in-out;
}

.menu-options-worker {
  position: absolute;
  background-color: #fff; /* Fondo blanco */
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.1); /* Sombra */
  border-radius: 8px; /* Bordes redondeados */
  padding: 10px;
  color: #333; /* Color del texto */
  z-index: 999; /* Asegura que el men est por encima de otros elementos */
  margin-left: -60px;
}

.menu-options-worker p {
  margin: 5px 0; /* Aade espacio entre las opciones del men */
  padding: 8px; /* Aade espacio alrededor del texto */
  cursor: pointer; /* Cambia el cursor al pasar sobre las opciones */
  transition: background-color 0.3s ease; /* Transicin suave para el cambio de color de fondo */
}

.menu-options-worker p:hover {
  background-color: #f5f5f5; /* Cambia el color de fondo al pasar el cursor sobre las opciones */
}


.icon:hover{
  transform: scale(1.5,1.5);
  cursor: pointer;
}

.user-info-worker {
  position: absolute;
  right: 90px; /* Centra horizontalmente */
  transform: translateY(-10%); /* Ajusta el centro */
  font-weight: 600;
}