* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; 
  font-family: 'Montserrat';
}

.navbar-container-worker {
  width: 100%;
  padding: 10px 0;
  background: #c62426;
  color: white;
}

.dashboard-worker {
  display: flex;
  gap: 10px;
  height: max-content;
  width: 100%; /* Asegura que el dashboard no se extienda ms all del ancho del .worker */
  overflow: auto; /* Aade desplazamiento si es necesario */
}

.table-container-worker {
  width: 78%; /* Asegrate de que la suma de este ancho y el de otros elementos no exceda el 100% */
  /* overflow: auto; Permite desplazamiento si el contenido interno es muy ancho */
  margin: 20px;
}
